const isNewsContent = (state = "-", {type, inputText}) => {
    switch (type) {
        case 'IS_NEWS_CONTENT':
            let inputValue = inputText
            
            state = inputValue;    
            return state;
        case 'IS_NEWS_CONTENT2':
            return state = false;
        default: 
            return 2;
    }
};

export default isNewsContent;